*,
*:before,
*:after {
  box-sizing: border-box; }

h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.05; }

a {
  color: inherit;
  text-decoration: none; }

@media screen and (max-width: 450px) {
  html {
    font-size: 3.5vw; } }

@media screen and (min-width: 450px) {
  html {
    font-size: 16px; } }

body {
  font-family: "Gothic A1", sans-serif;
  background-color: #222;
  color: #eee;
  line-height: 1.58; }

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 3000;
  padding: 0;
  text-align: right;
  padding: 0.5rem 1rem;
  height: 6rem;
  background-image: linear-gradient(to bottom, #1e2345, rgba(38, 44, 87, 0));
  color: white; }
  .header:after {
    content: "";
    display: table;
    width: 100%; }

.headerLeft {
  float: left; }

.headerLogo {
  display: inline-block;
  width: 11rem;
  padding: 1rem 0.5rem; }
  .headerLogo img {
    display: block;
    width: 100%; }

.headerRight {
  float: right; }

.headerLink {
  cursor: pointer;
  display: inline-block;
  padding: 1rem 0.5rem 0.9rem;
  margin: 0 0.2rem;
  border-bottom: 0.1rem solid transparent; }
  .headerLink:hover, .headerLink.active {
    border-color: currentColor; }
  .headerLink.active {
    color: #6dcff6; }

.main {
  min-height: 100vh; }

.footer {
  background-color: #222;
  color: #eee; }

.footerBox {
  margin: 0 auto;
  width: 100%;
  max-width: 50rem;
  padding: 1.5rem; }
  .footerBox:after {
    content: "";
    display: table;
    width: 100%; }

.footerLeft {
  float: left; }

.footerRight {
  float: right; }
